* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
}

ul {
  list-style: none;
}

.App {
  display: flex;
  flex-direction: column;
  margin: auto;
}

.photo {
  width: 100%;
  height: auto;
  box-shadow: 0px 10px 24px 7px rgba(163, 161, 163, 0.86);
}

.download {
  color: black;
  font-size: 1rem;
  margin: auto;
  display: block;
  text-align: center;
  text-decoration: none;
  margin-bottom: 5rem;
}

.download:hover {
  background-color: rgb(118, 206, 233);
  color: #ffffff;
}

.photo__wrapper {
  padding-top: 3vh;
}

.sidebar {
  width: 100%;
}

.social {
  display: flex;
  width: 80%;
  justify-content: space-around;
  margin: 3vh auto;
}

.nav {
  margin-top: 10px;
}

.nav__item {
  text-decoration: none;
  color: #000000;
  font-size: 1rem;
  text-align: center;
  display: block;
  margin: 2vh 0;
}

.nav__item:hover {
  text-decoration: none;
  background-color: rgb(118, 206, 233);
  color: #ffffff;
}

/* styles for transition group */
.page {
  position: absolute;
  top: 0;
  left: 0;
}

.fade-appear.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-appear-active .fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 800ms linear 500ms;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 800ms linear 0ms;
}

/* media queries */
@media (min-width: 320px) and (max-width: 468px) {
  .photo {
    width: 70%;
    height: auto;
    margin: auto;
  }
}

@media (min-width: 469px) and (max-width: 768px) {
  .photo {
    width: 70%;
    height: auto;
    margin: auto;
  }
}

@media (min-width: 769px) {
  .App {
    flex-direction: row;
  }

  .sidebar {
    width: 27%;
  }

  .photo__wrapper {
    padding: 3vh;
  }
}
